import React, { useState } from 'react';
import Modal from 'react-modal';
import axios from 'axios';
import './App.css';  // Import your custom styles if needed

const RandomValuePickerModal = ({ onSave }) => {
  const [modalIsOpen, setModalIsOpen] = useState(false);
  const [response, setResponse] = useState(null);
  const [selectedValue, setSelectedValue] = useState(null);

  const openModal = () => {
    setModalIsOpen(true);
  };

  const closeModal = () => {
    setModalIsOpen(false);
    setResponse(null);  // Reset the response when modal is closed
    setSelectedValue(null);  // Reset the selected value when modal is closed
  };



  const handleRandomClick = async () => {

      try {

        const data = {
          //make a single data/time object
          date: "",
          topic: "hello"
        };
        window.Telegram.bots.invokeWebViewCustomMethod(JSON.stringify(data)); // Send data as a JSON string

        // const response = await window.Telegram.bots.invokeWebViewCustomMethod({
        //   bot: { _: 'inputUserSelf' }, // Assuming you want to invoke the method on the bot associated with the current user
        //   custom_method: 'myCustomMethod', // Replace with your custom method identifier
        //   params: { // Provide method parameters here
        //     // Add any parameters your custom method requires
        //   },
        // });
        // console.log('Custom method response:', response);
        // Handle the response as needed
      } catch (error) {
        console.error('Error invoking custom method:', error);
      }

    // try {
    //   const res = await axios.get('http://localhost:8080/topic/random'); // Replace with your API endpoint
    //   setResponse(res.data);
    //   setSelectedValue(res.data);  // Save response data to selectedValue
    // } catch (error) {
    //   console.error('Error fetching random value:', error);
    // }

    // try {
    //   // Construct the method call
    //   const result = await window.Telegram.bots.sendCustomRequest(
    //     'custom_method_name', // Method name
    //     { key: 'value' } // Parameters to pass to the method
    //   );
      
    //   console.log('Response from custom method:', result);
    // } catch (error) {
    //   console.error('Error invoking custom method:', error);
    // }
  };

  const handleButtonClick = async () => {
    const req_id = 'unique_request_id'; // Generate a unique request ID for tracking
    const method = 'getUserProfile';
    const params = { userId: '12345' }; // Example user ID

    try {
      const result = await window.Telegram.WebApp.invokeWebViewCustomMethod({
        custom_method: method,
        params: params
      });

      setResponse(result.data);

      // Emit the custom_method_invoked event with the result
      const customMethodInvokedEvent = new CustomEvent('custom_method_invoked', {
        detail: {
          req_id: req_id,
          result: result
        }
      });
      window.dispatchEvent(customMethodInvokedEvent);
    } catch (error) {
      // Handle the error response

      // Emit the custom_method_invoked event with the error
      const customMethodInvokedEvent = new CustomEvent('custom_method_invoked', {
        detail: {
          req_id: req_id,
          error: error.message
        }
      });
      window.dispatchEvent(customMethodInvokedEvent);
    }
  };



  // const handleRandomClick = () => {
  //   if (window.Telegram && window.Telegram.WebApp) {
  //     const data = {
  //       //make a single data/time object
  //       date: selectedDate,
  //       topic: "hello",
  //       participantLimit: participantLimit,
  //       participantLevel: participantLevel
  //     };
  //     window.Telegram.WebApp.sendData(JSON.stringify(data)); // Send data as a JSON string
  //   }
  // };

  const handleIDoItClick = () => {
    setSelectedValue('no');
  };

  const handleSubmit = () => {
    onSave(selectedValue);
    closeModal();
  };

  return (
    <div className="picker-container">
      <button onClick={openModal}>Select Topic</button>
      <Modal
        isOpen={modalIsOpen}
        onRequestClose={closeModal}
        contentLabel="Select Value"
        className="modal"
        overlayClassName="overlay"
      >
        <h2>Choose an Action</h2>
        <button className="topic-button"  onClick={handleButtonClick}>Random</button>
        <button className="topic-button" onClick={handleIDoItClick}>I Do It</button>
        {response && <div>{JSON.stringify(response)}</div>}
        {selectedValue && (
          <button onClick={handleSubmit}>Submit</button>
        )}
        <button className="topic-button" onClick={closeModal}>Close</button>
      </Modal>
    </div>
  );
};

export default RandomValuePickerModal;
